.Reasons
{
    display: flex;
    gap: 2rem;
    padding: 0 2rem;
}

.left-r
{
    flex: 1;
    display: grid;
    grid-template-columns: repeat(3,1fr);
    gap: 1rem;
    grid-auto-rows: 1fr;
}

.left-r>img
{
    object-fit: cover;
}

.left-r>:nth-child(1)
{
    width: 12rem;
    grid-row: 1/3;
    height: 28rem;
}

.left-r>:nth-child(2)
{
    width: auto;
    height: 16rem;
    grid-column: 2/4;
}

.left-r>:nth-child(3)
{
    width: 14rem;
    grid-column: 2/3;
    grid-row: 2;
}

.left-r>:nth-child(4)
{
    width: 10rem;
    grid-column: 3/4;
    grid-row: 2;
    height: 11.2rem;
}

.right-r
{
    flex: 1;
    text-transform: uppercase;
    display: flex;
    flex-direction: column;
    gap: 1rem;
}

.right-r>span
{
    font-weight: bold;
    color: var(--orange);
}

.right-r>div
{
    color: white;
    font-size: 3rem;
    font-weight: bold;
}

.details-r
{
    display: flex;
    flex-direction: column;
    gap: 1rem;
}

.details-r>div
{
    display: flex;
    gap: 1rem;
    align-items: center;
    font-size: 1.2rem;
}

.details-r>div>img
{
    width: 2rem;
    height: 2rem;
}

.right-r>:nth-child(4)
{
    color: var(--gray);
    font-weight: normal;
}

.partners
{
    display: flex;
    gap: 1rem;
}

.partners>img
{
    width: 2.5rem;
}

@media screen and (max-width : 1300px)  
{
    .Reasons
    {
        display: flex;
        gap: 1.5rem;
        flex-direction: column;
    }

    .left-r 
    {
        width: fit-content;
        margin: auto;
    }
}

@media screen and (max-width : 820px)  
{
    .left-r
    {
        grid-auto-rows: auto;
        overflow: hidden;
    }

    .left-r>:nth-child(1)
    {
        width: 7rem;
        height: 17rem;
    }

    .left-r>:nth-child(2)
    {
        width: 15rem;
        height: 10rem;
    }

    .left-r>:nth-child(3)
    {
        width: 7rem;
    }

    .left-r>:nth-child(4)
    {
        width: 7rem;
        height: 6rem;
    }

    .right-r>div {
        font-size: 1.5rem;
    }

    .details-r>div {
        font-size: 0.7rem;
    }

    .details-r>div>img {
        width: 1.5rem;
        height: 1.5rem;
    }
}