.Programs
{
    display: flex;
    flex-direction: column;
    gap: 2rem;
    padding: 0 2rem;
}

.programs-header
{
    display: flex;
    gap: 5rem;
    text-transform: uppercase;
    font-weight: bold;
    font-size: 3rem;
    justify-content: center;
    color: white;
    font-style: italic;
}

.program-categories
{
    display: flex;
    gap: 1rem;
}

.category
{
    display: flex;
    flex-direction: column;
    gap: 1rem;
    color: white;
    background-color: gray;
    padding: 2rem;
    justify-content: space-between;
}

.category>:nth-child(1)
{
    height: 2rem;
    width: 2rem;
    fill: white;
}

.category>:nth-child(2)
{
    font-size: 1rem;
    font-weight: bold;
}

.category>:nth-child(3)
{
    font-size: 0.9rem;
    line-height: 25px;
}

.join-now
{
    display: flex;
    align-items: center;
    gap: 2rem;
}

.join-now>img
{
    width: 1rem;
}

.category:hover
{
    background: var(--planCard);
    cursor: pointer;
}



@media screen and (max-width : 1300px)  
{
    .programs-header
    {
        font-size: 2rem;
    }

   .program-categories
   {
    flex-direction: column;
   } 
}

@media screen and (max-width : 830px)  
{
    .programs-header
    {
        font-size: 1.3rem;
    }
}

@media screen and (max-width : 600px)  
{
    .programs-header
    {
        flex-direction: column;
        align-items: center;
        gap: 1rem;
    }
}